<template>
  <!-- 开发商账户详情 -->
  <el-dialog
    title="开发商账户详情"
    :visible.sync="dialogVisible.trunoff"
    width="40%"
    :before-close="handleClose"
  >
    <el-form
      :label-position="labelPosition"
      label-width="100px"
      :model="tDyFrom"
      class="demo-form-inline"
      ref="tDyFrom"
      :role="rules"
    >
      <el-form-item prop="dutyNo" label="税号">
        <el-input style="width:70%" v-model="tDyFrom.dutyNo"></el-input>
        <el-button style="width:80px; margin-left:10px;" @click="searchFn">搜索</el-button>
      </el-form-item>
      <el-form-item label="项目">
        <el-input style="width:90%;" disabled="disabled" :value="dialogVisible.projectName"></el-input>
      </el-form-item>
      <el-form-item prop="kfsName" label="开发商名称">
        <el-input style="width:90%;" placeholder="请输入开发商名称" v-model="tDyFrom.kfsName"></el-input>
      </el-form-item>
      <el-form-item prop="bankName" label="开户银行">
        <el-input style="width:90%;" placeholder="请输入开户银行" v-model="tDyFrom.bankName"></el-input>
      </el-form-item>
      <el-form-item prop="account" label="银行账户">
        <el-input style="width:90%;" placeholder="请输入银行账户" v-model="tDyFrom.account"></el-input>
      </el-form-item>
      <el-form-item prop="mobile" label="单位电话">
        <el-input style="width:90%;" placeholder="请输入单位电话" v-model="tDyFrom.mobile"></el-input>
      </el-form-item>
      <el-form-item prop="address" label="单位地址">
        <el-input style="width:90%;" placeholder="请输入单位地址" v-model="tDyFrom.address"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="resetForm()">取 消</el-button>
      <el-button type="primary" @click.native="postAddD">提交</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { addDutyNo, findKfsInfo } from "@/api/business/commission";
export default {
  props: {
    dialogVisible: {
      type: Object
    }
  },
  data() {
    return {
      labelPosition: "right",
      rules: {
        projectId: [{ required: true, message: "请输入项目", trigger: "blur" }],
        dutyNo: [{ required: true, message: "请输入税号", trigger: "blur" }],
        kfsName: [
          { required: true, message: "请输入开发商名称", trigger: "blur" }
        ],
        bankName: [
          { required: true, message: "请输入开户银行", trigger: "blur" }
        ],
        account: [
          { required: true, message: "请输入银行账户", trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "请输入单位电话", trigger: "blur" }
        ],
        address: [
          { required: true, message: "请输入单位地址", trigger: "blur" }
        ]
      },
      tDyFrom: {
        dutyNo: "",
        address: "",
        mobile: "",
        account: "",
        bankName: "",
        kfsName: "",
        projectId: ""
      }
    };
  },
  methods: {
    handleClose(dnoe) {
      this.$refs.tDyFrom.resetFields();
      // for(let key in this.tDyFrom){
      //   this.tDyFrom[key]=''
      // }
      // this.dialogVisible.trunoff = false;
      dnoe();
    },
    postAddD() {
      // 
      // console.log(this.tDyFrom);
      addDutyNo(this.tDyFrom).then(res=>{
        if(res.data.code==='000'){
            this.$message.success('开发商提交成功')
            this.dialogVisible.trunoff = false;
        }
      })
      
    },
    searchFn() {
      if (this.tDyFrom.dutyNo == "") {
        for (let key in this.tDyFrom) {
          this.tDyFrom[key] = "";
        }
        return false;
      }
      findKfsInfo({ dutyNo: this.tDyFrom.dutyNo })
        .then(res => {
          if (res.data.code === "000") {
            let obj = res.data.bizData;
            if (obj == undefined) {
              return;
            }
            for (let key in obj) {
              if (key === "name") {
                this.tDyFrom.kfsName = obj[key];
                continue;
              }
              if(key==='kfsId'){
                continue;
              }
              this.tDyFrom[key] = obj[key];
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 重置
    resetForm() {
      this.$refs.tDyFrom.resetFields();
    }
  },
  watch: {
    dialogVisible: {
      handler(val, old) {
        this.tDyFrom.dutyNo = val.dutyNo;
        this.tDyFrom.projectId = val.projectId;
        this.searchFn();
      },
      deep: true
    }
  }
};
</script>
<style lang="scss" scoped>
/deep/.el-form-item__content {
  display: flex;
  width: 90%;
 
}
/deep/.el-dialog {
  
  
}
</style>
